import * as T from '@microsoft/fast-element';
import './template.html';

                
//@ts-ignore                
let rwsTemplate: any = T.html`<div><div class="loader"></div></div>`;

import './styles/layout.scss';
const styles = T.css``;

const shadowOptions = {"mode":"open"};
import { RWSView, RWSViewComponent } from '../../index';

@RWSView('the-loader', null, { template: rwsTemplate, styles, options: {shadowOptions} })
class RWSLoader extends RWSViewComponent  {
  
    connectedCallback(): void {
        super.connectedCallback();
    }
}

RWSLoader.defineComponent();

export { RWSLoader };