import * as T from '@microsoft/fast-element';
import './template.html';

                
//@ts-ignore                
let rwsTemplate: any = T.html`<div class="upload_area">
    <section>
        <h3>Upload file</h3>
        <rws-progress :value="${x => x.uploadProgress}" min="1" max="100"></rws-progress>
        <article>
            <div class="drag-and-drop"> 
                <div @click="${x => !x.chosenFile ? x.onChoose() : (() => {})()}" class="upl-background"></div>
                ${T.when(x => !x.chosenFile, T.html`<p @click="${x => !x.chosenFile ? x.onChoose() : (() => {})()}" class="dropinfo">Drag and drop here to upload</p>`)}
                ${T.when(x => x.chosenFile, T.html`<div class="file-block">${ x => x.chosenFile.name} <span @click="${ x => x.removeFile() }" class="close-btn">X</span></div>`)}
            </div>
            <div class="upl-controls">
                <button @click="${ x => x.onChoose() }" class="outline">Choose file</button>                
            </div>
        </article>
        <button @click="${ x => x.onUploadStart() }" :disabled="${ x => x.chosenFile ? false : true }" appearance="accent" class="next">Upload</button>
    </section>
</div>`;

import './styles/layout.scss';
const styles = T.css`rws-progress {
  --accent-foreground-rest: $upl_primary;
}

.upload_area {
  color: var(--rws-uploader-text, #000);
  padding: 25px;
}
.upload_area * {
  box-sizing: border-box;
}
.upload_area section {
  display: block;
  contain: content;
  box-sizing: border-box;
  background: var(--rws-uploader-bg, #424242);
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.7);
}
.upload_area section::after {
  content: "";
  display: table;
  clear: both;
}
.upload_area section .drag-and-drop {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  border: 4px dashed var(--rws-uploader-border, #FFF);
  width: 100%;
  height: 100%;
  padding: 30px;
  margin-bottom: 30px;
  text-align: center;
  cursor: pointer;
}
.upload_area section .drag-and-drop .file-block {
  border: 1px solid var(--rws-uploader-border, #FFF);
  font-size: 10px;
  padding: 15px;
  border-radius: 15px;
}
.upload_area section .drag-and-drop .file-block .close-btn {
  border: 1px solid var(--rws-uploader-border, #FFF);
  padding: 5px;
  font-weight: bold;
  margin-left: 10px;
  border-radius: 15px;
}
.upload_area section .drag-and-drop.hover {
  position: relative;
}
.upload_area section .drag-and-drop.hover:before {
  content: "";
  background: var(--rws-uploader-secondary, #CCC);
  opacity: 0.4;
  position: absolute;
  width: 100%;
  height: 100%;
}
.upload_area section .drag-and-drop.hover p {
  font-weight: bold;
}
.upload_area section h3 {
  padding: 0 15px;
  color: var(--rws-uploader-text, #000);
}
.upload_area section article {
  padding: 15px;
}
.upload_area section button {
  background: var(--rws-uploader-btn-bg, #CCC);
  color: var(--rws-uploader-btn-text, #000);
  font-size: 14px;
  padding: 10px;
  box-shadow: none;
  border-radius: 5px;
  border-style: solid;
  border-color: var(--rws-uploader-btn-border, #000);
  border-width: 1px;
  fill: currentcolor;
  cursor: pointer;
}
.upload_area section button.outline {
  background: none;
}
.upload_area section button.next {
  float: right;
  margin-right: 10px;
  margin-bottom: 10px;
}`;

const shadowOptions = {"mode":"open"};
import { RWSView, RWSViewComponent, observable, attr } from '../../index';

@RWSView('rws-uploader', null, { template: rwsTemplate, styles, options: {shadowOptions} })
class RWSUploader extends RWSViewComponent  {

  @observable uploadProgress: number;

  @observable uploadedFile: File;
  @observable chosenFile: File;
  @observable uploadParams: any;

  @attr onFinish: (uploadResponse: any) => void;
  @attr onStart: (chosenFile: File, context: any) => Promise<unknown> = async (chosenFile: File) => chosenFile;
  @attr onProgress: (progress: number) => void = (progress: number) => null;


  async onUploadStart(): Promise<void>
  {    
        if(!this.uploadProgress){
            this.uploadProgress = 0;
        }

      const response = await this.onStart(this.chosenFile, this);
   
      if(response === null){
          return;
      }

      this.onFinish(response);

      this.uploadedFile = this.chosenFile;
      this.chosenFile = null;    
  }

  onChoose(): void
  {
      const _self = this;
      const fileInput = this.createFileInput();

      this.triggerUpload(fileInput);

      fileInput.addEventListener('change', () => {
          _self.chosenFile = fileInput.files[0]; 
          
          _self.uploadedFile = null;

          _self.removeFileInput(fileInput);      
      });
  }

  removeFile(){
      this.chosenFile = null;
  }

  private createFileInput(): HTMLInputElement
  {
      const fileInput: HTMLInputElement = document.createElement('input');
      fileInput.type = 'file';
      fileInput.style.display = 'none';


      this.shadowRoot.appendChild(fileInput);
      return fileInput;
  }

  private triggerUpload(fileInput: HTMLInputElement): void
  {
      fileInput.click();
  }

  private removeFileInput(fileInput: HTMLInputElement): void
  {
      this.shadowRoot.removeChild(fileInput);
  }

  uploadProgressChanged(oldV: any, newV: any)
  {
    
  }
}

RWSUploader.defineComponent();

export { RWSUploader };