import * as T from '@microsoft/fast-element';
import './template.html';

                
//@ts-ignore                
let rwsTemplate: any = T.html`<template
            role="progressbar"
            aria-valuenow="${x => x.value}"
            aria-valuemin="${x => x.min}"
            aria-valuemax="${x => x.max}"
        >
            ${T.when(
                x => typeof x.value === "number",
                T.html`
                    <div class="progress rws-progress" part="progress" slot="determinate">
                        <div
                            class="determinate"
                            part="determinate"
                            style="width: ${x => x.percentComplete}%"
                        ></div>
                    </div>
                `,
                T.html`
                    <div class="progress rws-progress" part="progress" slot="indeterminate">
                        <slot name="indeterminate">                           
                        </slot>
                    </div>
                `
            )}
</template>`;

import './styles/layout.scss';
const styles = T.css`:host {
  align-items: center;
  display: flex;
  contain: content;
  outline: none;
  height: calc(var(--design-unit) * 1px);
  margin: calc(var(--design-unit) * 1px) 0;
}

.progress {
  background-color: var(--neutral-fill-rest);
  border-radius: calc(var(--design-unit) * 1px);
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  position: relative;
}

.determinate {
  background-color: var(--accent-foreground-rest);
  border-radius: calc(var(--design-unit) * 1px);
  height: 100%;
  transition: all 0.2s ease-in-out;
  display: flex;
}

.indeterminate {
  border-radius: calc(var(--design-unit) * 1px);
  display: flex;
  height: 100%;
  overflow: hidden;
  position: relative;
  width: 100%;
}

.indeterminate-indicator-1 {
  animation: indeterminate-1 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
  background-color: var(--accent-foreground-rest);
  border-radius: calc(var(--design-unit) * 1px);
  height: 100%;
  opacity: 0;
  position: absolute;
  width: 40%;
}

.indeterminate-indicator-2 {
  position: absolute;
  opacity: 0;
  height: 100%;
  background-color: var(--accent-foreground-rest);
  border-radius: calc(var(--design-unit) * 1px);
  width: 60%;
  animation: indeterminate-2 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

@keyframes indeterminate-1 {
  0% {
    opacity: 1;
    transform: translateX(-100%);
  }
  70% {
    opacity: 1;
    transform: translateX(300%);
  }
  70.01% {
    opacity: 0;
  }
  100% {
    opacity: 0;
    transform: translateX(300%);
  }
}
@keyframes indeterminate-2 {
  0% {
    opacity: 0;
    transform: translateX(-150%);
  }
  29.99% {
    opacity: 0;
  }
  30% {
    opacity: 1;
    transform: translateX(-150%);
  }
  100% {
    transform: translateX(166.66%);
    opacity: 1;
  }
}`;

const shadowOptions = {"mode":"open"};
import { RWSView, RWSViewComponent, observable, attr } from '../../index';
import {
    nullableNumberConverter,
} from '@microsoft/fast-element';

@RWSView('rws-progress', null, { template: rwsTemplate, styles, options: {shadowOptions} })
class RWSProgress extends RWSViewComponent  {

    @attr({ converter: nullableNumberConverter })
    public value: number | null;
    protected valueChanged(): void {
        this.updatePercentComplete();
    }


    @attr({ converter: nullableNumberConverter })
    public min: number;
    protected minChanged(): void {
        if (this.$fastController.isConnected) {
            this.updatePercentComplete();
        }
    }

    
    @attr({ converter: nullableNumberConverter })
    public max: number;
    protected maxChanged(): void {
        if (this.$fastController.isConnected) {
            this.updatePercentComplete();
        }
    }

    
    @observable
    public percentComplete: number = 0;

    public connectedCallback(): void {
        super.connectedCallback();
        this.updatePercentComplete();        
    }

    private updatePercentComplete(): void {
        const min: number = typeof this.min === 'number' ? this.min : 0;
        const max: number = typeof this.max === 'number' ? this.max : 100;
        const value: number = typeof this.value === 'number' ? this.value : 0;
        const range: number = max - min;

        this.percentComplete =
            range === 0 ? 0 : Math.fround(((value - min) / range) * 100);            
    }
}

RWSProgress.defineComponent();

export { RWSProgress };